// Browse blank view screen

.contentScreen#screen-blank
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    text-align: center

    -webkit-touch-callout: none
    -webkit-user-select: none
    -khtml-user-select: none
    -moz-user-select: none
    -ms-user-select: none
    user-select: none

    width: 100%
    height: 100%

