// Structural divisions

// Structural resets
html
    background-color: var(--colBackground)


body, html
    margin: 0px
    padding: 0px
    height: 100%

pre
    @include code-font
    font-size: var(--size5)

    margin-left: 2em
    margin-right: 2em
    background-color: var(--colCodeGrey)
    padding: 0.5em
    overflow-x: auto
    border: 1px solid var(--colBorder)

pre code
    padding: 0px

code
    background-color: var(--colCodeGrey)
    padding-left: 0.5em
    padding-right: 0.5em

hr
    width: 50%

    margin-top: 2em
    margin-bottom: 2em

    border-top: 0px
    border-bottom: 1px solid var(--colBorder)


// Main application holder (full screen)
// Holds sideBar and contentHolder
#pageHolder
    //padding-top: var(--headerbarHeight)

    display: flex
    flex-direction: row

    //height: calc(100% - var(--headerbarHeight))
    height: 100%
