// Font variables

@font-face
  font-family: 'Frutiger'
  font-style: normal
  font-weight: 400
  src: url("../fonts/FrutigerLT-LightCn.ttf") format('truetype')

@font-face
  font-family: 'Frutiger'
  font-style: normal
  font-weight: 600
  src: url("../fonts/Frutiger_57_Condensed.ttf") format('truetype')

@font-face
  font-family: 'Source Code Pro'
  font-style: normal
  font-weight: 400
  src: url("../fonts/SourceCodePro-Regular.ttf") format('truetype')


$font-sans:         'Frutiger', 'Arial Narrow', 'Open Sans', "Arial", sans-serif
$font-mono:         'Source Code Pro', monospace

$font-wt:       400
$font-bold-wt:  600


// General mixins
@mixin sans-font
    font:
        family: $font-sans
        weight: $font-wt
        style: normal

@mixin sans-bold
    font:
        family: $font-sans
        weight: $font-bold-wt
        style: normal

@mixin header-font
    font:
        family: $font-sans
        weight: $font-wt
        style: normal

@mixin code-font
    font:
        family: $font-mono
        weight: $font-wt
        style: normal




// Default font is sans
body
    @include sans-font
    font-size: var(--sizeBase)
    cursor: default
    color: var(--colText)
    font-feature-settings: "salt"

// Headers
h1, h2, h3, h4, h5, h6
    @include header-font
    cursor: default

    margin-top: 2.75rem
    margin-bottom: 1.05rem
    font-weight: $font-bold-wt
    line-height: 1.15



h1
    font-size: var(--size1)

h2
    font-size: var(--size2)

h3
    font-size: var(--size3)

h4
    font-size: var(--size4)

small
    font-size: var(--size5)


// Margins on text blocks
p, ul, ol, small
    margin-top: 1.15rem
    margin-bottom: 1.15rem

// Reset anchor colours
a
    color: var(--colHighlight)

strong
    color: var(--colPrimary)
