#sideBar
    width: calc(300px - 2em)
    height: calc(100% - 2em)
    flex-shrink: 0
    flex-grow: 0
    background-color: var(--colSideBar)

    -webkit-touch-callout: none
    -webkit-user-select: none
    -khtml-user-select: none
    -moz-user-select: none
    -ms-user-select: none
    user-select: none

    padding: 1em

    // display: flex
    // flex-direction: column
    // align-items: flex-start

    h1
        margin-top: 0.5em
        color: var(--colRed)
        background: linear-gradient(90deg, var(--gradient1) 0%, var(--gradient2) 150%)
        -webkit-background-clip: text
        -webkit-text-fill-color: transparent


@media only screen and (max-width : 700px)
    #sideBar
        display: none
