table
	color: var(--colText)
	border-collapse: collapse
	font-size: var(--sizeBase)

	thead
		background-color: var(--colTableHead)
		color: var(--colBackground)
		font-weight: bold
		padding: 0.5em
		td
			padding-top: 0.4em
			padding-bottom: 0.4em

	th
		padding-left: 0.5em
		padding-right: 0.5em
		border-right: 2px solid var(--colBackground)
		height: 2em

	tbody tr
		border-bottom: 2px solid var(--colBorder)
		height: 2em

	td
		padding-right: 0.7em
		padding-left: 0.5em
		border: none
