// Chart view screen

.modal#modal-QR
    visibility: hidden

    width: 100%
    height: 100%
    position: absolute
    top: 0px
    left: 0px

    display: flex
    flex-direction: row
    align-items: center
    justify-content: center

    background-color: var(--colBackground)

    #qrHolderModal
        width: 100%
        height: 100%
        max-width: 500px
        max-height: 500px

        img
            width: 100%

.modal#modal-BG
    width: 100%
    height: 100%
    position: absolute
    top: 0px
    left: 0px
    z-index: -999999

    display: flex
    flex-direction: row
    align-items: center
    justify-content: center

    img
        width: 100%
        height: 100%
        object-fit: cover
        object-position: center top
        filter: blur(2px)

.modal#backgroundFilter
    width: 100%
    height: 100%
    position: absolute
    top: 0px
    left: 0px
    z-index: -99999

    background-color: var(--colRedTr)

.contentScreen#screen-chart
    display: flex
    flex-direction: row
    align-items: center
    justify-content: center
    text-align: center

    -webkit-touch-callout: none
    -webkit-user-select: none
    -khtml-user-select: none
    -moz-user-select: none
    -ms-user-select: none
    user-select: none

    width: 100%
    height: 100%

    max-width: 100% !important

    #qrHolder
        margin-right: 40px
        width: calc(20% - 40px)
        min-width: 200px
        flex-grow: 2

        img
            width: 100%

    #chartHolder
        width: 100%
        height: 100%
        flex-shrink: 1
        // border: 1px solid red

        svg
            text
                @include sans-font

            #boundingRect
                fill: none
                stroke: var(--colGrey3)
                stroke-width: 0px

            .seriesBlock
                stroke: var(--colWhiteTr)
                stroke-width: 2px

            #seriesBlock-0
                fill: var(--colGreenLt)

            #seriesBlock-1
                fill: var(--colYellow)

            #seriesBlock-2
                fill: var(--colRed)

            .blockLabel
                fill: var(--colWhite)
                text-anchor: middle
                font-size: 2.2vmin
                font-weight: bold
                alignment-baseline: central

            .blockLabelHidden
                display: none

            .tick text
                font-size: 3vmin
                font-weight: bold

            .xAxis text
                font-size: 2vmin


            .deltaLabel
                fill: var(--colWhite)
                text-anchor: middle
                font-size: 2.7vmin
                font-weight: bold
                alignment-baseline: central

            .deltaCircle
                fill: var(--colBackground)
                stroke: var(--colTr)
                stroke-width: 2px

            .deltaCirclePositive
                fill: var(--colGreenLt)

            .deltaCircleNegative
                fill: var(--colRed)



            .deltaPath
                stroke: var(--colBlack)
                stroke-width: 3px
                fill: none

            .deltaPathPositive
                stroke: var(--colGreenLt)

            .deltaPathNegative
                stroke: var(--colRed)



            .arrowHead
                stroke-width: 0px
                fill: var(--colBlack)

            .arrowHeadPositive
                stroke: var(--colGreenLt)
                fill: var(--colGreenLt)

            .arrowHeadNegative
                stroke: var(--colRed)
                fill: var(--colRed)

            .legEntry text
                font-weight: bold
                fill: var(--colText)

            .legendGreen path
                fill: var(--colGreenLt)
                stroke: none

            .legendOrange path
                fill: var(--colYellow)
                stroke: none

            .legendRed path
                fill: var(--colRed)
                stroke: none
