// Header bar with optional tools
#headerBar
    position: fixed
    top: 0px
    left: 0px
    height: var(--headerbarHeight)
    width: 100%
    background-color: var(--colBlue)
    background: linear-gradient(90deg, var(--gradient1) 0%, var(--gradient2) 100%)

    display: flex
    justify-content: flex-start
    align-items: center

    -webkit-touch-callout: none
    -webkit-user-select: none
    -khtml-user-select: none
    -moz-user-select: none
    -ms-user-select: none
    user-select: none

    // TO HIDE BUTTONS - set display: none
    display: none

    .toolbarButton
        flex-shrink: 0
        height: 30px
        width: 32px
        border: none
        background-color: transparent
        margin-top: 0px
        margin-bottom: 0px

        margin-left: 1em
        padding-left: 5px
        padding-right: 5px
        padding-top: 0px
        padding-bottom: 0px

    #menuButton
        display: none
        div
            width: 100%
            height: 2px
            background-color: var(--colBackground)
            margin-top: 4px
            margin-bottom: 4px
            margin-right: 0px

    #themeButton
        display: none
        div
            float: left

        div.circleLeft
            width: 6px
            height: 12px
            background-color: var(--colThemeButton2)
            border: 3px solid var(--colThemeButton1)
            border-top-left-radius: 12px
            border-bottom-left-radius: 12px
            border-right: 0

        div.circleRight
            width: 6px
            height: 12px
            background-color: var(--colThemeButton1)
            border: 3px solid var(--colThemeButton2)
            border-top-right-radius: 12px
            border-bottom-right-radius: 12px
            border-left: 0

    #pageTitle
        padding-left: 1em
        font-size: var(--size4)
        font-weight: bold
        color: var(--colBackground)
        flex-grow: 1
        margin: 0px
        overflow-x: hidden
        text-overflow: ellipsis
        white-space: nowrap
