input
    color: var(--colText)
    font-size: var(--sizeBase)
    background-color: var(--colBackground)

    -webkit-appearance: none
    -moz-appearance: none
    border-style: 1px solid var(--colText)
    border-radius: 5px
    padding: 10px

    border-style: solid
    border-width: 1px
    border-color: var(--colText)
    border-radius: 5px
    padding: 7px
    margin-top: 5px
    margin-bottom: 1em
    margin-left: 0px

input[type=date]
    min-width: 80px
    min-height: 21px

textarea
    width: 100%
    color: var(--colText)
    font-size: var(--sizeBase)
    background-color: var(--colBackground)

    -webkit-appearance: none
    -moz-appearance: none
    border-style: 1px solid var(--colText)
    border-radius: 5px
    padding: 10px

    border-style: solid
    border-width: 1px
    border-color: var(--colText)
    border-radius: 5px
    padding: 7px
    margin-top: 5px
    margin-bottom: 1em
    margin-left: 0px

label
    font-size: var(--sizeBase)
    font-weight: bold

select
    color: var(--colText)
    font-size: var(--sizeBase)
    background-color: var(--colBackground)

    -webkit-appearance: none
    -moz-appearance: none
    border-style: 1px solid var(--colText)
    border-radius: 5px
    padding: 10px

    border-style: solid
    border-width: 1px
    border-color: var(--colText)
    border-radius: 5px
    padding: 7px
    margin-top: 5px
    margin-bottom: 1em
    margin-left: 0px

select:disabled
    background-color: var(--colSideBar)
    color: var(--colGrey)
    border-color: var(--colGrey)

button
    font-size: var(--sizeBase)
    background-color: var(--colBackground)
    border: 1px solid var(--colText)
    color: var(--colText)
    border-radius: 5px
    padding: 11px
    margin-right: 3px
    margin-bottom: 6px


button.blueButton
    background-color: var(--colBlue)
    color: var(--colBackground)
    border: 1px solid var(--colBlue)
    font-weight: bold

button.greyButton
    background-color: var(--colGrey)
    color: var(--colBackground)
    border: 1px solid var(--colGrey)
    font-weight: bold

button.redButton
    background-color: var(--colRed)
    color: var(--colBackground)
    border: 1px solid var(--colRed)
    font-weight: bold

button.blueOutlineButton
    color: var(--colBlue)
    border: 1px solid var(--colBlue)
    background-color: transparent

button.greyOutlineButton
    color: var(--colGrey)
    border: 1px solid var(--colGrey)
    background-color: transparent

button.redOutlineButton
    color: var(--colRed)
    border: 1px solid var(--colRed)
    background-color: transparent

button.smallButton
    padding-top: 5px
    padding-bottom: 5px
    font-size: var(--size5)

button.iconButton
    display: flex
    flex-direction: row
    align-items: center
    padding-top: 5px
    padding-left: 10px
    padding-right: 16px
    padding-bottom: 3px

    svg
        width: 30px
        fill: var(--colText)

