// App style definition

:root
    // Brand colours
    --colRed:           #d81e05

    --colGrey:          #54565b
    --colGrey2:         #898b8e
    --colGrey3:         #d1d0ce

    --colBlue:          #002f87
    --colBlueLt:        #00A1E0

    --colPurple:        #672767
    --colPurpleLt:      #B4008E

    --colGreen:         #205b41
    --colGreenLt:       #00AD69

    --colBrown:         #691c33
    --colBrownLt:       #aa2d2a

    --colOrange:        #ff6d37
    --colYellow:        #ffb648
    --colYellowLt:      #f9e08e

    --colWhite:         #ffffff
    --colBlack:         #000000

    --colPrimary:       var(--colRed)

    // Muted colours
    --colBlueMuted:     #2b5799     // 60% bri
    --colOrangeMuted:   #805000     // 50% bri
    --colGreenMuted:    #164d16     // 30% bri
    --colRedMuted:      #661118     // 40% bri
    --colPurpleMuted:   #331433     // 20% bri

    // Transparent colours (change alpha to suit purpose)
    --colRedTr:         #C83F3ED1
    --colTr:            #00000000
    --colWhiteTr:       #ffffff77


    // Theme dependant colours
    --colText:           var(--colWhite)
    --colBackground:     var(--colWhite)
    --colCodeGrey:       var(--colGrey2)

    // Side bar
    --colSideBar:        var(--colGrey3)

    // Tables
    --colTableHead:      var(--colPrimary)

    // Highlighted text (links, bold)
    --colHighlight:      var(--colPrimary)

    // Borders (subtle use, e.g. pre block, table rows, hr)
    --colBorder:         var(--colGrey3)

    // Geometry configuration variables
    --menuBarWidth:       75px
    --contentsBarWidth:   220px

    // Header bar gradient colour
    --gradient1:        var(--colPrimary)
    --gradient2:        var(--colBrownLt)


    // Theme button toggle
    --colThemeButton1:   var(--colBackground)
    --colThemeButton2:   var(--gradient1)


    // Font size variables
    // Using 1.250 (Major Third) font size scaling
    // p element is base size (1em)
    // h4 to h1 increase in factors of 1.24

    --sizeBase:          1.0em
    --size0:             3.052em
    --size1:             2.441em
    --size2:             1.953em
    --size3:             1.563em
    --size4:             1.250em
    --size5:             0.8em


    // Configuration variables
    --headerbarHeight:   50px

    // Small screen threshold (hide nav for smaller screens)
    --smallScreenSize:   700px


// Transitions for smooth theme switching
html, html>*, nav, input, pre, td, div
    transition-property: background-color, fill, background, border-color
    transition-duration: 0.2s
    transition-timing-function: ease-in

svg path
    transition-property: fill
    transition-duration: 0.2s
    transition-timing-function: ease-in


// Dark theme colours
html.darkTheme
    --colText:           var(--colWhite)
    --colBackground:     var(--colBlack)
    --colContents:       #111111
    --colLogo:           #eeeeee
    --colCodeGrey:       #111111
    --colBorder:         #555555
    --colSideBar:        var(--colGrey)

    // Theme button invert
    --colThemeButton2:   var(--colBackground)
    --colThemeButton1:   var(--gradient1)


// Imports
@import '_text'					// Text elements
@import '_structure'			// Basic structural elements

@import '_tables'               // Structural elements
@import '_forms'                // Form elements

@import '_headerbar'            // Page header bar (top)
@import '_sidebar'              // Sidebar (left)

@import '_draft'                // Draft tag

// Views
@import '_contentHolder'        // Holder of views (right)
@import 'views/_blank'          // Blank screen (startup / no selection)
@import 'views/_chart'          // Chart screen - main view

// Components
