#draftTag

    display: none

    position: fixed
    top: 25px
    right: -50px

    width: 200px
    height: 40px

    font-size: var(--size3)
    font-weight: bold

    text-align: center

    color: var(--colRed)

    border-top: 3px dashed var(--colRed)
    border-bottom: 3px dashed var(--colRed)

    background-color: var(--colBackground)

    -webkit-transform: rotate(45deg)
    -moz-transform: rotate(45deg)
    -o-transform: rotate(45deg)
    transform: rotate(45deg)

    div#draftTextHolder
        height: 100%
        width: 100%

        display: flex
        flex-direction: column
        justify-content: center
        align-items: center

        p
            margin: 0px
