// Main screen content

div#contentHolder
    width: calc(100% - 2em)
    height: 100%
    flex-shrink: 1
    flex-grow: 1

    padding: 1em
    padding-top: 0px
    padding-bottom: 0px

    overflow: auto

    .contentScreen
        max-width: 800px
        margin-left: auto
        margin-right: auto
        background-color: var(--colBackgroundTr)
        // -webkit-backdrop-filter: blur(5px)
        backdrop-filter: blur(5px)

        padding-left: 1em
        padding-right: 1em
        width: calc(100% - 2em)
